// Masking inputs
// from: https://github.com/estelle/input-masking/
// modified by @dmkrupin

/* Libary CSS */
.shell {
    position: relative;
    line-height: 1;
  span {
    position: absolute;
    left: 3px;
    top: 1px;
    color: red;
    pointer-events: none;
    z-index: -1;
    visibility: hidden;

    i { /* any of these 3 will work */
      color: transparent;
      opacity: 0;
      visibility: hidden;
    }
  }
}

input.masked,
.shell span {
  font-size: 16px;
  font-family: monospace;
  padding-right: 10px;
  background-color: transparent;
}
